.optionWindow {
    color: var(--mediumGray1);
    margin-bottom: 0.7rem;
    box-shadow: 0px 1px 3px 0px #000000;
}

.optionWindow .titleBar {
    display: flex;
    justify-content: space-between;
    padding: 0.25rem;
    background-color: rgba(0,0,0,0.25);
}

.optionWindow:hover .titleBar {
    color: var(--lightGray1);
}


.optionWindow .titleBar button {
    color: var(--mediumGray1);
}

.optionWindow .titleBar button:hover {
    color: var(--lightGray1);
}

.optionWindow .title {
    font-size: 0.75rem;
}